<template>
  <div class="admin-update">
      <div class="form-wrapper">
        <h1>Update Book</h1>
        <form action="" @submit.prevent="sendForm()" class="edit-form" enctype="multipart/form-data" >
            <div class="form-name">
                <label for="name">Name</label>
                <input 
                    type="text" 
                    v-model="form.name" 
                    name="name" 
                    id="name"
                >
            </div>
            <div class="form-genre">
                <label for="author">Author</label>
                <select name="author" v-model="form.author_id"  id="genre">
                    <option value="null">Select Author</option>
                    <option 
                        v-for="(author,index) in allAuthor" 
                        :key="index"
                        :value="author._id" 
                        :selected="author.name == book.author || null "
                    >{{ author.name }}</option>
                </select>
            </div>
            <div class="form-genre">
                <label for="genre">Genre</label>
                <select name="genre" v-model="form.genre"  id="genre">
                    <option value="null">Select Genre</option>
                    <option 
                        v-for="(genre,index) in allGenre" 
                        :key="index" 
                    >{{ genre.name }}</option>
                </select>
            </div>
            <div class="form-image">
                <label for="image">Image</label>
                <input 
                    type="file" 
                    ref="image" 
                    @change="handleUpload" 
                    name="image" 
                    id="image"
                >
            </div>
            <img :src="setImage" id="image-view" alt="">
            <p @click="deleteImg"  class="btn btn-danger btn-delete w-25">Delete</p>
            <div class="form-description">
                <label for="description">Description</label>
                <textarea 
                    v-model="form.description" 
                    name="description" 
                    id="description" 
                    cols="30" 
                    rows="10"
                ></textarea>
            </div>
                <div v-if="error.length > 0" class="error-wrapper">
                    <ul class="list-error">
                        <li
                            v-for="(err,index) in error"
                            :key="index"
                        >
                            {{ err }}
                        </li>
                    </ul>
                </div>
                <input type="submit" class="btn btn-primary btn-submit" value="Change Data">
        </form>
      </div>
  </div>
</template>

<script>
import { getAllGenre, getAllAuthor, getBook, updateBook } from "@/utils/index"

export default {
    name : "adminBookEdit",
    data() {
        return {
            allGenre : [],
            allAuthor : [],
            book : [],
            form : {
                name : "",
                genre : null,
                photo : null,
                author_id : null,
                description : "",            
            },
            imgDefault : false,
            error : [],
            limit : 5,
            key :this.$route.params.key || " " ,
            page : this.$route.params.page,
        };
    },
    async mounted() {
        try {
            const bookId = this.$route.params.bookId;
            const book =  await getBook(bookId);
            this.book = book;
            this.setForm(this.book);
            this.allGenre = await getAllGenre();
            this.allAuthor = await getAllAuthor();
        } catch(err) {
            this.$router.push({
                name : 'admin-book',
                params : {
                    status : "error"
                }
            })
        }
    },
    methods : {
        async sendForm() {
            const isError = this.validateForm(this.form);
            if( isError.length > 0  ) return this.error = isError.map( err => err.msg );
            try {
                const bookId = this.$route.params.bookId;
                const dataForm = {
                    ...this.form,
                    bookId,
                    imgDefault : this.imgDefault,
                }
                await updateBook(dataForm,this.page,this.limit,this.$route.query);
                await this.$router.push({
                    name : "admin-book-status",
                    params : {
                            status : "success",
                    },
                    query : {
                        page : this.page,
                    }
                });            
            } catch(err) {  
                console.log(err);
                await this.$router.push({
                    name : "admin-book-status",
                    params : {
                            status : "error",
                    },
                }); 
            }
        },
        handleUpload() {
            const imgInput = this.$refs.image.files[0];
            this.form.photo = imgInput;
            const img = document.getElementById('image-view');
            img.src = URL.createObjectURL(imgInput);
            window.onload = () => {
                URL.revokeObjectURL(img.src);
            }
        },
        deleteImg() {
            const img = document.getElementById('image-view');
            img.src = null,
            this.form.photo = null;
        },
        setForm(data) {
            const { name, author, genre, description} = data;
            this.form = {
                name,
                author_id : author ? author._id : "",
                genre : genre || "",
                description,
            }
        },
        validateForm(input) {
            const err = [];
            const img = document.getElementById('image-view');
            if( !input.name || input.name == "" ) err.push({ msg : 'Name is required '});
            if( !input.author_id || input.author_id == "null" ) err.push({ msg : 'Author is required '});
            if( !input.genre || input.genre == "null" ) err.push({ msg : 'Genre is required '});
            if( !input.description || input.description == "null" ) err.push({ msg : 'Description is required '});
            if( input.photo && input.photo.size / 1000 > 2000 ) err.push({ msg: 'Your file is too large'});
            if( encodeURI(this.setImage) == img.src  ) this.imgDefault = true;
            return err;
        }
    },
    computed : {
        setImage() {
            const photo = {...this.book.photo};
            if(!photo.filename) return null;
            return `http://localhost:5000/img-card/${this.book.photo.filename}`; 
        }
    }
}
</script>

